//these functions are mostly used in the construction of other lexical functions
let greed = (next) => {
    return (current) => {
        if (typeof current === 'function' && !current.completed) {
            return (...args) => {
                return greed(next)(current(...args));
            }
        }
        return next(current);
    }
}

let contextual = (next) => (context) => {
    if (context) {
        return greed((finishedContext) => {
            return next(finishedContext);
        })(context);
    }
    return greed((val) => next(val));
};

let continuable = (context) =>
    greed((content) =>
    (continuedContent) =>
        greed((resolvedContinuedContent) => {
            return `${content} ${resolvedContinuedContent?resolvedContinuedContent:''}`;
        }
        )( (typeof continuedContent === 'function')
            ? continuedContent(pastContext(context))
            : continuedContent
        )
);

let activeContext = (context) => {
    return {
        target: context.target
            ? {
                ...context.target,
                 preferred: context.target.active,
            } : undefined,
        zone: context.zone
            ? {
                ...context.zone,
                preferred: context.zone.active,
            } : undefined,
    }
};

let pastContext = (context) => {
    return {
        target: context.target
            ? {
                ...context.target,
                 preferred: context.target.historical,
            } : undefined,
        zone: context.zone
            ? {
                ...context.zone,
                preferred: context.zone.historical,
            } : undefined,
    }
}

let line = () =>
    (context) =>
    greed((line) =>
    (newLine) => {
        return greed((resolvedNewLine)=>{
            return `${line}${resolvedNewLine?`\n${resolvedNewLine}`:''}`;
        })( (typeof newLine === 'function')
            ? newLine(pastContext(context))
            : newLine
        );
    }
);

let entityForms = ({noObjects, limit}) => {
    if (noObjects && limit === 1) return {
        singular: 'creature',
        base: 'creature',
        possesive: 'creature\'s',
        type: 'creature',
    }
    if (!noObjects && limit === 1 ) return {
        singular: 'creature or object',
        base: 'creature or object',
        possesive: 'creature or object\'s',
        type: 'creature or object',
    }
    if (!noObjects && limit !== 1) return {
        singular: 'creature or object',
        base: 'creatures or objects',
        plural: 'creatures or objects',
        possesive: 'creatures or objects\'',
        type: 'creature or object',
    }
    if (noObjects && limit !== 1) return {
        singular: 'creature',
        base: 'creatures',
        plural: 'creatures',
        possesive: 'creatures\'',
        type: 'creature or object',
    }
};

//functions able to be used when creating cards
const lexical = new (class LexicalFunctions{
    contextChange = () => contextual((oldContext) => (newContext) => greed((content) => {
        return content(newContext(oldContext));
    }));

    none = () => () => undefined;

    newContext = () => contextual((oldContext) => {
        return this.contextChange()(oldContext)(this.none());
    });

    targetEntity = ({sourceEntity='caster', range=0, limit=1, disposition=undefined, noObjects=false}) => {
        let entity = entityForms({noObjects, limit});
        return activeContext({
            target: {
                active: `target ${entity.base}`,
                historical: `targeted ${entity.base}`,
                typeEnforcement: `${entity.type}`,
                repetition: limit===1?'it':'them',
            },
            zone: {
                active: `target ${entity.possesive} zone${limit!==1?'s':''}`,
                historical: `targeted ${entity.possesive} zone${limit!==1?'s':''}`,
                typeEnforcement: 'zone',
                repetition: limit===1?'it':'them',
            }
        });
    };

    targetZone = ({sourceEntity='caster', range=0, limit=1}) => activeContext({
        zone: {
            active: 'target zone',
            historical: 'targeted zone',
            typeEnforcement: 'zone',
            repetition: limit===1?'it':'them',
        }
    });

    may = () => { //probably needs context
        return greed((optional)=>{
            if (optional.charAt(0) !== optional.charAt(0).toLocaleLowerCase()) optional = optional.charAt(0).toLocaleLowerCase() + optional.slice(1);
            return `You may ${optional}`;
        });
    };

    entityIn = ({limit=1, noObjects=false}) => {
        let entity = entityForms({noObjects, limit});
        return greed((context) => {
            return activeContext({
                target: {
                    active: (limit
                        ? `up to ${limit} ${entity.base} in ${context.zone.preferred}`
                        : `each ${entity.singular} in ${context.zone.preferred}`),
                    historical: `targeted ${entity.base}`,
                    typeEnforcement: `${entity.type}`,
                    repetition: limit===1?'it':'them',
                },
                zone: {...context.zone},
            })
        });
    };

    makeAttackAgainst = ({type, range, advantage, defense}) => {
        advantage = Math.abs(advantage) > 0
            ? ` with ${Math.abs(advantage)} rank${Math.abs(advantage)>1?'s':''} of ${advantage>0?'advantage':'disadvantage'}`
            : ``;
        return contextual((context) => {
            return line()(context)(
                continuable(context)(`Make a ${range} ${type} attack${advantage} against the ${defense} defense of ${context.target.preferred}.`)
            );
        })
    };

    onHit = () => {
        return contextual((context) => {
            return (effect) => {
                return continuable(context)(`\n@child On hit, ${effect(pastContext({
                    target: {
                        historical: `hit ${context.target.typeEnforcement}`,
                        typeEnforcement: context.target.typeEnforcement,
                        repetition: `it`
                    }
                }))}.`);
            };
        });
    };

    dealDamage = (damage) => {
        return contextual((context) => {
            return `deal ${
                Object.entries(damage||{}).map(([key, value]) => {
                    return `${value} ${key} damage`;
                }).reduce((acc, val, index, array)=> {
                    return `${acc}${array.length!==2?',':''}${index===array.length-1?' and':''} ${val}`
                })
            }${context?` to the ${context.target.preferred}`:''}`;
        });
    };

    ifDamaged = ({type}) => {
        return contextual((context) => {
            return greed((effect) => {
                return continuable(context)(`\n@child If a ${context.target.typeEnforcement} took ${type?`${type} `:''}damage from this, ${effect(context)}.`);
            });
        });
    };

    invert = ({tag}) => {
        return contextual((context)=>{
            return `${context.target.repetition} must invert a card${tag?` with the ${tag} tag`:''} if possible`;
        });
    } 

    add = (...args) => {
        return args.reduce((acc, val) => {
            return `${acc} + ${val}`;
        });
    }

    ref = (val) => `@${val}`;

    end = () => undefined;
})();



const interpret = (text) => {
    let result = x=>x;
    while(text) {

        let [current, ...remainder] = text.split(/;/);
        text = remainder.reduce((acc,val)=>`${acc};${val}`);

        let currentFunctionName = current.match(/\w+/)
        currentFunctionName = currentFunctionName
            ? currentFunctionName[0]
            : undefined;
        let currentInner  = current.match(/\(.*\)/)
        currentInner = currentInner
            ? currentInner[0]
            : undefined;
        if (currentInner) currentInner = interpretInner(currentInner);

        result = result(lexical[currentFunctionName](currentInner));
    }

    return result;
};

const blobify = (content) => {
    let blobs = [];
    let functionArgs = content.match(/\w+\(.*\)/);
    while (functionArgs) {
        functionArgs = functionArgs[0];
        let index = blobs.length;
        blobs.push(functionArgs);
        content = content.replace(functionArgs, `$\{${index}}`);
        functionArgs = content.match(/\w+\(.*\)/);
    }
    return [
        content,
        blobs,
    ]
};

const unblobify = ({content, blobs}) => {
    blobs.forEach((val, index) => {
        content = content.replace(`$\{${index}}`, val);
    });
    return content;
};

//todo update to handle nested dictionaries
const interpretInner = (text) => {
    if(!text) return;
    if(text.match(/\(\)/)) return;
    let content = text.match(/{(.*)}/);
    content = content
        ? content[1]
        : undefined;
    if(!content) return {};
    let blobs;

    ([content, blobs] = blobify(content));
    content = content.replace(/,/g, '\n');
    content = unblobify({content, blobs});

    let dict = {};
    content = content.split(/\s/g);
    content.forEach((val) => {
        let [key, value] = val.split(/:/);
        if(!key) return;
        dict[key] = interpretFunctionChain(value);
    })
    return dict;
};

const interpretFunctionChain = (text) => {
    if (!text.match(/^\w+\(.*\)/)) {
        try{
            return Number(text);
        } catch(e){}
        return text;
    }
    let functionName = text.match(/^(\w+)\(.*\)/)[1];
    let args = text.match(/^\w+\((.*)\)/)[1];
    let blobs;
    ([args, blobs] = blobify(args));
    args = args.replace(/,/g, '\n');
    args = unblobify({content: args, blobs,});
    args = args.split(/\s/);
    args = args.map((val) => interpretFunctionChain(val));
    return lexical[functionName](...args);
};

export default (s) => {
    if(Array.isArray(s)) {
        return s;
    }
    return interpret(s.replace(/\s/g,'')).split(/\n/).filter(x=>x);
};